<template>
  <div class="user-container">
    <!-- 头部个人信息展示 -->
    <div class="header">
      <!-- 头像及用户名 -->
      <div class="user-info">
        <!-- 头像 -->
        <img class="avatar" :src="$store.state.avatar" alt="" />
        <!-- 用户名及申请认证 -->
        <div class="username">
          <h4>{{ baseUserInfo.name }}</h4>
          <span>申请认证</span>
        </div>
      </div>
      <!-- 动态、关注、粉丝 -->
      <div class="other">
        <div class="other-item">
          <span>{{ baseUserInfo.art_count }}</span>
          <span>动态</span>
        </div>
        <div class="other-item">
          <span>{{ baseUserInfo.follow_count }}</span>
          <span>关注</span>
        </div>
        <div class="other-item">
          <span>{{ baseUserInfo.fans_count }}</span>
          <span>粉丝</span>
        </div>
      </div>
    </div>
    <!-- 操作区域 -->
    <div class="operating-area">
      <van-cell title="编辑资料" center is-link to="/user_editor">
        <template #icon>
          <van-icon name="edit" size="0.42667rem" />
        </template>
      </van-cell>
      <van-cell title="小思同学" center is-link to="/chat">
        <template #icon>
          <van-icon name="chat-o" size="0.42667rem" />
        </template>
      </van-cell>
      <van-cell title="退出登录" is-link center @click="logout">
        <template #icon>
          <van-icon name="warning-o" size="0.42667rem" />
        </template>
      </van-cell>
    </div>
  </div>
</template>

<script>
import { Dialog } from 'vant'
import { removeToken } from '@/utils/token'
export default {
  name: 'User',
  data() {
    return {
      baseUserInfo: {}
    }
  },
  created() {
    this.getBaseUserInfo()
  },
  methods: {
    async getBaseUserInfo() {
      const res = await this.$API.reqUserBaseInfo()
      this.baseUserInfo = res.data.data
      // 将用户头像存入vuex, 小思同学要使用
      this.$store.commit('GET_AVATAR', res.data.data.photo)
    },
    // 退出登录
    logout() {
      this.$dialog
        .confirm({
          title: '提示',
          message: '这就溜了?'
        })
        .then(() => {
          // on confirm
          // 移除token
          removeToken()
          // 跳转到login
          this.$router.replace('/login')
        })
        .catch(() => {
          // on cancel
        })
    }
  }
}
</script>

<style lang="less" scoped>
.user-container {
  font-size: 16px;
}
.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 196px;
  padding: 20px 15px;
  box-sizing: border-box;
  background-color: #007bff;
  color: #fff;
}
.user-info {
  display: flex;
  align-items: center;
  padding-top: 10px;
  .avatar {
    width: 60px;
    height: 60px;
    margin-right: 10px;
    border-radius: 50%;
    background-color: #fff;
  }
  .username {
    h4 {
      margin: 0;
      margin-bottom: 8px;
    }
    span {
      padding: 2px 5px;
      border-radius: 2px;
      background-color: #fff;
      color: #007bff;
      font-size: 12px;
    }
  }
}
.other {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-bottom: 10px;
  .other-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.van-icon {
  margin-right: 4px;
}
</style>
